import React from 'react';
import {Card, Modal, Table} from "antd";
import {connect} from "react-redux";

import {Wrapper} from "./styled"
import {clickLink, initSelectableList, doCancelSelect, sendSelectCourse } from "./store/actionCreators";
import UserBox from "../../components/UserBox";

const columns =
    [
        {
            title: "",
            dataIndex: "cid",
            key: "cid",
            render: text => <a href="javascript:;" corsecode={text}>{text}</a>
        },
        {
            title: "",
            dataIndex: "cname",
            key: "cname",
            render: text => <a href="javascript:;" corsecode={text}>{text}</a>
        },
        {
            title: "",
            dataIndex: "teacher",
            key: "teacher"
        },
        {
            title: "",
            dataIndex: "score",
            key: "score"
        },
        {
            title: "",
            dataIndex: "duce",
            key: "duce"
        },
        {
            title: "",
            dataIndex: "selected",
            key: "selected"
        },
        {
            title: "",
            dataIndex: "capacity",
            key: "capacity"
        }
    ]

class SelectSourse extends React.Component {

    componentWillMount() {
        /**
         * ajax,render
         */
        this.props.getInitList();
    }

    render() {
        const s = this.props.course;
        const sc = {
            cid:  s.cid,
            sid: this.props.id
        };
        return (
            <Wrapper>
                <UserBox/>
                <Card>
                    <p>{this.props.selectable.length}</p>
                    <Table columns={columns} dataSource={this.props.courses}
                           onRow={(recode, index) => {
                               return {
                                   onClick: (recode) => {
                                       this.props.openModel(this.props.selectable[index].cid)
                                   }
                               }
                           }
                           }/>
                </Card>
                {console.log(this.props.showCourseInfo)}
                <Modal title={s.cname} visible={this.props.showCourseInfo}
                       onOk={()=>this.props.handleOnOk(sc)} onCancel={this.props.handleOnCancel}
                        okText="" cancelText="">
                    <h4>{s.teacher}</h4>
                    <h4>{s.score}</h4>
                    <h4>{s.date}&nbsp;{s.time}~{s.time+s.acount}</h4>
                    <h4>{s.space}</h4>
                    <h4>{s.capacity}</h4>
                    <h4>{s.duce}</h4>
                </Modal>
            </Wrapper>
        );
    }
}

const mapStateToProps = (state) => {
    /**
     * array,
     */
    const courses = [];
    state.getIn(['selectcourse', 'selectable']).map((item) => {
        courses.push(item);
    });
    return {
        id: state.getIn(['logined','id']),
        courses: courses,
        showCourseInfo: state.getIn(['selectcourse', 'showCourseInfo']),
        selectable: state.getIn(['selectcourse', 'selectable']),
        course: state.getIn(['selectcourse','course'])
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getInitList() {
            dispatch(initSelectableList());
        },
        openModel(index) {
            dispatch(clickLink(index));
        },
        handleOnOk(value){
            dispatch(sendSelectCourse(value))
        },
        handleOnCancel(){
            dispatch(doCancelSelect());
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SelectSourse);