import React from 'react';
import {Card, Table, Modal, Button} from 'antd';
import { connect } from "react-redux";

import { actionCreators } from "./store";
import UserBox from "../../components/UserBox";

const { Column } = Table; 

class Courses extends React.Component{

    componentWillMount(){
        this.props.toGetList(this.props.tid);
    }

    render(){
        const { created, showCourseInfo, courseInfo, isShow, clickOk, clickCancel } = this.props;
        if (this.props.fresh){
            this.props.toGetList(this.props.tid);
            this.props.notFresh();
        }
        return (
            <div>
                <UserBox/>
                <Card>
                    <Button type="primary" icon="reload" onClick={()=>this.props.toGetList(this.props.tid)}
                            style={{position: "fixed", top:65, right: 30}}></Button>
                    <Table dataSource={created} size="small">
                        <Column title="" dataIndex="cid" key="cid"
                            render={(text, record)=>(
                                <a href="javascript:;" onClick={()=>{
                                    showCourseInfo(text)
                                }}>{text}</a>
                            )}/>
                       
                        <Column title="" dataIndex="cname" key="cname" 
                            render={(text, record)=>(
                                <a href="javascript:;" onClick={()=>{
                                    showCourseInfo(record.cid)
                                }}>{text}</a>
                            )}
                            />
                        
                        <Column title="" dataIndex="capacity" key="capacity" />

                        <Column title="" dataIndex="score" key="score" />

                        <Column title="" dataIndex="duce" key="duce" />

                        <Column title="" dataIndex="time" key="time" render={(text, record)=>(
                            <span>{record.date}&nbsp;{record.time}~{record.time + record.acount}</span>
                        )}/>

                        <Column title="" dataIndex="space" key="cid" />

                        <Column title="" key="code"
                            render={(text, record) => (
                                <a href="javascript:;" onClick={()=>{
                                    showCourseInfo(record.code);
                                }}></a>
                            )}
                        />
                                              
                    </Table>
                </Card>
                <Modal title="" visible={isShow} 
                    onOk={()=>clickOk(courseInfo.cid)} onCancel={clickCancel}>
                    <div><span></span><span>{courseInfo.cname}</span></div>
                    <div><span></span><span>{courseInfo.cid}</span></div>
                    <div><span></span><span>{courseInfo.capacity}</span></div>
                    <div><span></span><span>{courseInfo.time}</span></div>
                    <div><span></span><span>{courseInfo.space}</span></div>
                    <div><span></span><span>{courseInfo.duce}</span></div>
                    <div><span></span><span>{courseInfo.score}</span></div>
                </Modal>
            </div>
        );
    }
}

const mapStateToProps = (state) =>{
    return{
        created: state.getIn(["courses","courses"]),
        isShow: state.getIn(["courses", "wantShowInfo"]),
        courseInfo: state.getIn(["courses","courseInfo"]),
        tid: state.getIn(['logined', 'id']),
        fresh: state.getIn(['courses', 'fresh'])
    }
};

const mapDispatchToProps = (dispatch) =>{
    return{
        toGetList(tid){
            /**
             * 
             */
            dispatch(actionCreators.getCreatedCourseList(tid));
        },
        showCourseInfo(data){
            dispatch(actionCreators.gotoShowCoure(data));
        },
        clickOk(value){
            dispatch(actionCreators.doDeleteCourse(value));
        },
        clickCancel(){
            dispatch(actionCreators.doNotShowCourse());
        },
        notFresh(){
            dispatch(actionCreators.changeFreshStatus());
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps) (Courses);